___CSS_LOADER_EXPORT___.push([module.id, `body {
  margin: 0;
  padding: 0;
  font-family: "Aldrich", sans-serif;
  background-color: #f9f9f9;
  color: #222;
}

#app {
  max-width: 700px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  text-align: center;
  margin-bottom: 30px;
}

.header h1 {
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
  color: #2c2c2c;
}

.tagline {
  font-size: 1rem;
  color: #666;
}

.main-container {
  background: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.05);
}

.input-group {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
}

#addressInput {
  flex: 1;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

#searchBtn {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #bb5fbb;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.2s;
}

#searchBtn:hover {
  background-color: #a84ea8;
}

.results {
  margin-top: 20px;
  background-color: #f2f2f2;
  border-radius: 6px;
  padding: 15px;
}

.results h2 {
  margin-top: 0;
  color: #333;
}

.results a {
  display: inline-block;
  margin-top: 10px;
  color: #0056b3;
  text-decoration: none;
}

.results a:hover {
  text-decoration: underline;
}

.footer {
  margin-top: 40px;
  text-align: center;
  font-size: 0.9rem;
  color: #888;
}

.heart {
  color: red;
}

.hidden {
  display: none;
}
@media (max-width: 600px) {
  .input-group {
    flex-direction: column;
  }

  #searchBtn {
    width: 100%;
  }

  #zipInput {
    width: 100%;
  }

  .main-container {
    padding: 15px;
  }
}

#result {
  margin-top: 20px;
}

.card {
  background: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}

.card h2 {
  margin-top: 0;
}

#search {
  margin-bottom: 20px;
